/* purgecss start ignore */
.branding-link {
  text-decoration: none;
  color: $c-black;
}

.sfgov-logo__container {
  @include media($narrow-screen) {
    padding: 12px 0;
  }
}

.sfgov-logo {
  margin-top: 3px;

  @include media($narrow-screen) {
    margin-top: 0;
  }
}

.sfgov-logo__image {
  display: block;

  img {
    display: inline-block;
    vertical-align: middle;
  }
}

.sfgov-logo--seal {
  width: 32px;
  height: 32px;

  @include media($medium-screen) {
    height: 56px;
    margin-right: 4px;
    width: 56px;
  }
}

.sfgov-logo--lockup {
  width: 86.18px;
  height: 18px;

  @include media($medium-screen) {
    width: 150px;
    height: 29px;
  }
}

.sfgov-logo--campaign {
  max-width: 260px;
}

a.branding-link:focus .sfgov-logo__image {
  @include focus-outline;
  outline-offset: 7px;
}
/* purgecss end ignore */
